import './GameCard.scss'
const test = n => window.addEventListener('keydown',(e)=>{if(e.key==='Alt')alert(n);if(e.key==='Control')console.log(n)})
const {log} = console
function GameCard({props}) {
    return(
        <div className="card">
            <div className="card__title">{props.name}</div>
            <div style={{background:`url(${props.image}`}} className="card__img"></div>
            <div className="card__description">{props.text}</div>
            <div className="card__state">{props.type}</div>
        </div>
    )
}
export default GameCard;