import { Box, Grid, Paper, Stack, TextField, MenuItem, Select, FormControl, InputLabel, Typography, Divider } from "@mui/material"

const FormOrder = () => {
    return (
        <Box
            sx={{
                display: 'flex',
                justifyContent: 'center'
            }}
        >
            <Box 
                component='form'
                sx={{
                    width: '100%',
                    px: '15px'
                }}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12} md={8}>
                        <Paper 
                            elevation={8}
                            sx={{
                                p: '10px'
                            }}
                        >
                            <Stack spacing={2}>
                                <TextField 
                                    variant="outlined"
                                    label=" "
                                />
                                <Box
                                    sx={{
                                        display: 'flex',
                                        justifyContent: 'space-between'
                                    }}
                                >
                                    <TextField 
                                        variant="outlined"
                                        label=" email"
                                        type="email"
                                        sx={{
                                            flexBasis: '45%'
                                        }}
                                    />
                                    <TextField 
                                        variant="outlined"
                                        label="  9XXXXXXXXX"                                        
                                        sx={{
                                            flexBasis: '45%'
                                        }}
                                    />
                                </Box>
                                <FormControl fullWidth>
                                    <InputLabel id="contact-city"> </InputLabel>
                                    <Select
                                        labelId="contact-city"
                                        id="select-city"
                                        // value={age}
                                        label=" "
                                        // onChange={handleChange}
                                    >
                                        <MenuItem value={10}></MenuItem>
                                        <MenuItem value={20}></MenuItem>
                                        <MenuItem value={30}></MenuItem>
                                    </Select>                                
                                </FormControl>
                            </Stack>
                        </Paper> 
                    </Grid> 
                    <Grid item xs={12} md={4}>
                        <Paper 
                            elevation={8}
                            sx={{
                                p: '10px'
                            }}
                        >
                            <Stack spacing={2}>
                                <TextField 
                                    variant="outlined"
                                    label=" "
                                    type="number"                                        
                                />
                                <Divider />
                                <Box
                                    sx={{
                                        textAlign: 'center'
                                    }}
                                >
                                    <Typography variant="h4">
                                        1.000 RUB
                                    </Typography>
                                </Box>                                
                            </Stack>
                        </Paper>                         
                    </Grid> 
                </Grid>
            </Box>
        </Box>
    )
}

export default FormOrder