import { Box, Button } from "@mui/material"
import { DataGrid } from "@mui/x-data-grid"
import { useNavigate } from "react-router-dom"

const Admin = () => {
    const navigate = useNavigate()

    const columns = [
        {
            field: 'id',
            headerName: ' ',            
        },
        {
            field: 'name',
            headerName: ' ',            
        },
        {
            field: 'email',
            headerName: 'Email',            
        },
        {
            field: 'address',
            headerName: '',            
        },
        {
            field: 'phone',
            headerName: '',            
        },
        {
            field: 'quantity',
            headerName: '',            
        },
        {
            field: 'status',
            headerName: '',            
        },        
    ]
    const rows = [
        {
            id: 1,
            name: 'Name1',
            email: 'email@mail.ru',
            address: 'Noginsk',
            phone: '624365432',
            quantity: 2,
            status: '',
        },
        {
            id: 2,
            name: 'Name2',
            email: 'apapa@mail.ru',
            address: 'Moscow',
            phone: '3253245657',
            quantity: 1,
            status: ' ',
        },
        {
            id: 3,
            name: 'Name3',
            email: '@mail.ru',
            address: '',
            phone: '276567523',
            quantity: 10,
            status: '',
        },
    ]

    const handleClick = () => {
        localStorage.removeItem('token')
        navigate('/login')
    }

    return (
        <>
            <Box sx={{ height: 400, width: '100%' }}>
                <DataGrid
                    rows={rows}
                    columns={columns}
                    pageSize={2}
                    rowsPerPageOptions={[5]}
                    checkboxSelection
                    disableSelectionOnClick
                    experimentalFeatures={{ newEditingApi: true }}
                />
            </Box>
            <Box 
                component='div'
                sx={{ mt: '20px', textAlign: 'center' }}
            >
                <Button
                    variant='outlined'
                    onClick={handleClick}
                >
                    
                </Button>
            </Box>
        </>
    )
}

export default Admin