import { Alert, Snackbar } from "@mui/material"
import { useEffect } from "react"
import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { getCode, setCode } from "../../service"
import Loader from "../../share/components/Loader"

const status = ['request', 'apply', 'disabled']

const Login = () => {

    const navigate = useNavigate()

    const [state, setState] = useState({
        isLoading: false,
        btnStatus: 'request',
        message: '',
        open: false,
        severity: null,
        code: '',
        reqTime: ''
    })

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return
        }
        setState({...state, open: false})
    }

    const handleClick = async () => {
        if (state.btnStatus === 'request') {
            setState({...state, isLoading: true})
            //const { status, message } = getCode()
            getCode()
            const { status, message } = {status: true, message: 'Error'}
            
            if (status) {
                setState({
                    ...state, 
                    isLoading: false,
                    btnStatus: 'apply',
                    open: true,
                    message: message,
                    severity: 'success',
                    reqTime: message
                })
            } else {
                setState({
                    ...state, 
                    isLoading: false,
                    btnStatus: 'disabled',
                    open: true,
                    message: message,
                    severity: 'error',
                    reqTime: message
                })
            }
        }

        if (state.btnStatus === 'apply') {
            setState({...state, isLoading: true})
            const { status, token, message } = applyPin()
            setCode({code: state.code})
            if (status) {
                setState({
                    ...state, 
                    isLoading: false
                })

                localStorage.setItem('token', token)
                navigate('/admin')
            } else {
                setState({
                    ...state, 
                    isLoading: false
                })
            }
        }
    }

    const applyPin = () => {
        return {status: true, token: '12431ewr$%%^5fgsdf', message: ''}
        // return {status: false, token: null, message: 'Error message'}
    }

    return (
        <div className="container">
            <div className="row my-5">
                <div className="col-4 offset-4 text-center">
                    {state.btnStatus === 'apply' && <input 
                        className="form-control"
                        type="text"
                        value={state.code}
                        onChange={(e) => setState({
                            ...state,
                            code: e.target.value
                        })}
                    />}
                </div>
            </div>
            <div className="row">
                <div className="col-4 offset-4 text-center">
                    {state.btnStatus !== 'disabled' && <button 
                        className={state.btnStatus === 'request' ? 'btn btn-primary' : 'btn btn-success'}
                        onClick={handleClick}
                        disabled={state.isLoading}
                    >
                        {
                            state.isLoading 
                            ? <Loader /> 
                            : state.btnStatus === 'request' ? '' : ''
                        }                        
                    </button>}
                </div>
            </div>
            <div className="row">
                {state.btnStatus === 'disabled' &&
                    <div className="col-10 offset-1 text-center alert alert-danger">
                           pin  - {state.reqTime}            
                    </div>
                }
            </div>
            <Snackbar 
                open={state.open} 
                autoHideDuration={6000} 
                onClose={handleClose}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right'
                }}
            >
                <Alert 
                    onClose={handleClose} 
                    severity={state.severity} 
                    sx={{ width: '100%' }}
                >
                    {state.message}
                </Alert>
            </Snackbar>            
        </div>
    )
}

export default Login