import { useEffect } from 'react';
import { useRef } from 'react';
import './Cursor.scss'
const test = n => window.addEventListener('keydown',(e)=>{if(e.key==='Alt')alert(n);if(e.key==='Control')console.log(n)})
const {log} = console
function Cursor({props}) {
    const cursor = useRef();
    
    function customCursor(){
        window.addEventListener('mousemove', function(e) {
            cursor.current.style.left = e.clientX + 'px'
            cursor.current.style.top = e.clientY + 'px'
           
            if (e.target.classList.contains('target') || e.target.tagName == 'A' || e.target.tagName == 'BUTTON') {
                cursor.current.classList.add('pointer')
    
            }else cursor.current.classList.remove('pointer')
            
        })
        
    }
    useEffect(() =>{
        customCursor()
    },[])
    return(
        <div ref={cursor} className="cursor"></div>
    )
}
export default Cursor;