const rootUrl = 'http://localhost:5000/' //'http://94.253.15.230:3000/'
const sendCode = 'send-code'
const postCode = 'post-code'

export const getCode = async () => {
    const response = await fetch(`${rootUrl}${sendCode}`, {method: 'POST'})
    if (response.ok) {
        return { status: true, message: '  ' }
    } else {
        return { status: false, message: '   .' }
    }
}

export const setCode = async (code) => {
    console.log(JSON.stringify(code))

    const response = await fetch(
        `${rootUrl}${postCode}`,
        {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(code)
        }
    )

    if (response.ok) {
        return { status: true }
    }
}