import { useState } from "react";
import { useEffect } from "react";
import { useRef } from "react";
import './Cardlist.scss';
import GameCard from "../../components/GameCard/GameCard";
import { darkTheme } from "../../utils/themeChangeUtil";
import Cursor from "../../components/Cursor/Cursor";

const {log} = console
function Cardlist(params) {
    let newCard = 0
    let cursor = useRef();
    const cardNumber = Number(window.location.search.slice(1, (window.location.search.length))) || 1
    function customCursor(){
        window.addEventListener('mousemove', function(e) {
            cursor.current.style.left = e.clientX + 'px'
            cursor.current.style.top = e.clientY + 'px'
           
            if (e.target.classList.contains('target') || e.target.tagName == 'A' || e.target.tagName == 'BUTTON') {
                cursor.current.classList.add('pointer')
    
            }else cursor.current.classList.remove('pointer')
            
        })
        
    }
    

    darkTheme()

    customCursor()
    async function getCard(id){
        
        await fetch(
            '/getcard',
            {
                method: 'POST',
                body: JSON.stringify(id),
            }
        ).then(res => res.json()
        ).then(data => setCard(data))
    }
    useEffect(() => {
        getCard(cardNumber)
    },[])
    const [card, setCard] = useState({})
    return(
        <div data-dark={darkTheme()} className='Cardlist'>
            <div className="main-screen">
                <div className="main-screen__button-container button-container">
                    <svg onClick={() => getCard(card.id - 1 || 1)} className="button-container__prev-button target" viewBox="0 0 5 9">
                        <path d="M0.419,9.000 L0.003,8.606 L4.164,4.500 L0.003,0.394 L0.419,0.000 L4.997,4.500 L0.419,9.000 Z"></path>
                    </svg>
                    <svg onClick={() => getCard(card.id + 1 || 1)} className="button-container__next-button target" viewBox="0 0 5 9">
                        <path d="M0.419,9.000 L0.003,8.606 L4.164,4.500 L0.003,0.394 L0.419,0.000 L4.997,4.500 L0.419,9.000 Z"></path>
                    </svg>
                </div>
                <div className="main-screen__card-number"> <span id="card-number">{card.id}</span><span>/</span><span id="cards-length">{card.length}</span></div>
                <div className="main-screen__card-wrapper card-wrapper">

                    <GameCard props={card}/>
                    
                </div>
                <h1 className="card-name">{card.name}</h1>
            </div>
            <div className="description">

            </div>
            <div className="loader"> 
                <h2> </h2>
            </div>
                <Cursor/>
        </div>
    )
}
export default Cardlist